let hourTensId = document.getElementById("hour-tens");
let hourDigitsId = document.getElementById("hour-digits");
let minuteTensId = document.getElementById("minute-tens");
let minuteDigitsId = document.getElementById("minute-digits");
let secondTensId = document.getElementById("second-tens");
let secondDigitsId = document.getElementById("second-digits");

function getTime() {
  let time = new Date();
  let hour = time.getHours();
  let minute = time.getMinutes();
  let second = time.getSeconds();
  let hourTens = parseInt(hour / 10);
  let hourDigits = hour - 10 * hourTens;
  let minuteTens = parseInt(minute / 10);
  let minuteDigits = minute - 10 * minuteTens;
  let secondTens = parseInt(second / 10);
  let secondDigits = second - 10 * secondTens;
  hourTensId.className = `num num${hourTens}`;
  hourDigitsId.className = `num num${hourDigits}`;
  minuteTensId.className = `num num${minuteTens}`;
  minuteDigitsId.className = `num num${minuteDigits}`;
  secondTensId.className = `num num${secondTens}`;
  secondDigitsId.className = `num num${secondDigits}`;
}
getTime();

setInterval(() => {
  getTime();
}, 1000);
